.bottom-notification{
  transition: opacity 3000ms ease-in;
  background: #EB5757;
  text-align: center;
  padding: 20px;
  color: #FFFFFF;
  font-family: 'Gilroy', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 138%;
  width: 100%;
  position: fixed;
  bottom: 0;
  z-index: 10;
}