@import'src/app/styles/variables';

.btn{
  position: relative;
  background-color: #585CC6;
  color: #FFFFFF;
  font-family: 'Gilroy', serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 18px;
  padding: 17px 28px;
  border-radius: 2px;
  z-index: 1;

  @media (max-width: $md3) {
    padding: 12px 6px;
    font-size: 14px;

    img{
      margin: 0;
    }
  }

  img{
    margin-right: 10px;
  }

  &:disabled{
    background: #8A8A8A;
  }

  &:after {
    content: '';
    position: absolute;
    height: 100%;
    width: 0;
    left: 0;
    top: 0;
    background: #696DC8;
    transition: all .4s ease;
    z-index: -1;
  }

  @media (any-hover: hover) {
    &:enabled:hover:after {
      width: 60%;
      z-index: -1;
    }
  }

  &:active:not([disabled]) {
    background: #696DC8;

    &:hover:after {
      width: 100%;
      z-index: -2;
      transition: all 0s ease;
    }
  }

  &_white{
    border: 1px solid #585CC6;
    background: #FFFFFF;
    color: #585CC6;

    &:after {
      background: #EEEFF9;
    }

    &:active:not([disabled]) {
      background: #C2C4FF;
    }

    &:disabled{
      background: #E0E0E0;
    }
  }

  &_red{
    background: #EB5757;

    &:after {
      background: #F96D6D;
    }

    &:active:not([disabled]) {
      background: #FF7171;
    }
  }

  &_img{
    padding: 12px;
    @media (max-width: $md3) {
      width: 42px;
    }
  }
}

